.footer__info-box:first-child {
  width: 120px;
}
.footer__info-box:last-child {
  width: 100px;
  margin-right: 0;
}
.footer__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.footer__info-link {
  width: 100%;
  text-decoration: none;
  font-size: 11px;
  font-weight: 400;
  line-height: 14px;
  margin-bottom: 10px;
  color: #ffffff;
}
.footer__info-link:active {
  color: #e94646;
}
.route__map {
  width: 100%;
  height: 400px;
}
@media (min-width: 768px) {
  .footer {
    min-height: 400px;
  }
  .footer__wrapper {
    padding: 50px;
  }
  .footer__box {
    align-items: flex-end;
    outline: 1px solid red;
  }
  .footer__search-form {
    width: 400px;
  }
  .footer__search-input {
    width: 400px;
  }
  .footer__logo {
    margin-right: 80px;
  }
  .footer__social {
    margin-right: 50px;
  }
  .footer__social-item:active {
    transform: scale(0.8);
  }
}
@media (min-width: 1360px) {
  .footer {
    min-height: 500px;
  }
  .footer__wrapper {
    position: relative;
    padding: 80px 75px;
    display: flex;
  }
  .footer__search-form {
    width: 485px;
  }
  .footer__search-input {
    width: 485px;
  }
  .footer__search-input::placeholder {
    font-size: 16px;
  }
  .footer__search-form {
    position: relative;
    margin-bottom: 50px;
  }
  .footer__search-btn {
    background-size: 22px 22px;
  }
  .footer__search-btn:hover {
    background-size: 23px 23px;
  }
  .footer__search-btn:active {
    background-size: 21px 21px;
  }
  .footer__logo {
    margin-right: 0;
  }
  .footer__logo-link {
    position: relative;
    font-size: 18px;
    margin-bottom: 40px;
    transition: color 0.5s 0.2s;
  }
  .footer__logo-link:active {
    opacity: 0.8;
  }
  .footer__logo-link::after {
    position: absolute;
    left: 0;
    bottom: -2px;
    content: "";
    width: 0;
    height: 2px;
    opacity: 0;
    background-color: #e94646;
    transition: width 0.5s, opacity 1s;
    transition-delay: 0.1s;
  }
  .footer__logo-link:hover.footer__logo-link::after {
    width: 100%;
    opacity: 1;
  }
  .footer__social {
    margin-right: 0;
  }
  .footer__social-item {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    box-sizing: content-box;
    transform: scale(0.9);
    transition: transform 0.3s;
  }
  .footer__social-item:hover,
  .footer__social-item:focus {
    transform: scale(1);
  }
  .footer__social-item:active {
    transform: scale(0.8);
  }
  .footer__social-link img {
    width: 60px;
    height: 60px;
  }
}
